// This part of store deals with how data is presented to user: what channels
// / montage are shown, which filters are applied, etc.

import {
  SET_VISIBLE_CHANNELS,
  SET_INPUT_MONTAGE,
  SET_BIPOLAR_MONTAGE,
  SET_DEFAULT_AMPLITUDE,
  SELECT_CHANNEL,
} from '../actions/viewMode';
import { SET_HEADER } from '../actions/header';

const bipolarNames = [
  'fp1-f7',
  'f7-t3',
  't3-t5',
  't5-o1',
  'fp1-f3',
  'f3-c3',
  'c3-p3',
  'p3-o1',
  'fz-cz',
  'cz-pz',
  'fp2-f4',
  'f4-c4',
  'c4-p4',
  'p4-o2',
  'fp2-f8',
  'f8-t4',
  't4-t6',
  't6-o2',
]; // TODO move to common place, perhaps constants

const initialState = {
  channelDefaults: {
    amplitude: 5, // No real meaning, arbitrary multiplier
    inverted: false,
    frequencyFilter: null,
    displayAs: 'plot', // hmm...
  },
  perChannelSettings: {
    /*
    channelId: {
      amplitude
      inverted
      frequencyFilter
      ...etc
    }
    */
  },
  visibleChannels: [
    // string id
  ],
  inputChannels: [
    // This is a denormalized part of store. Get rid of it in the future.
    // string id
  ],
  userSpecifiedChannels: [
    // string formula (doubles as identifier)
  ],
};

export function viewModeReducer(state = initialState, action) {
  let newState = state;
  const { type, payload } = action;

  switch (type) {
    case SET_HEADER:
      newState = {
        ...state,
        visibleChannels: payload.header.signals.map(({ label }) => label),
        inputChannels: payload.header.signals.map(({ label }) => label),
      };
      break;

    case SET_VISIBLE_CHANNELS:
      newState = {
        ...state,
        visibleChannels: payload,
      };
      break;

    case SET_INPUT_MONTAGE:
      newState = {
        ...state,
        visibleChannels: [...state.inputChannels],
      };
      break;

    case SET_BIPOLAR_MONTAGE:
      newState = {
        ...state,
        visibleChannels: [...bipolarNames],
      };
      break;

    case SET_DEFAULT_AMPLITUDE:
      newState = {
        ...state,
        channelDefaults: {
          ...state.channelDefaults,
          amplitude: payload,
        },
      };
      break;

    case SELECT_CHANNEL:
      newState = {
        ...state,
        perChannelSettings: {
          ...state.perChannelSettings,
        },
      };
      newState.perChannelSettings[payload] =
        newState.perChannelSettings[payload] || {};
      for (let channel in newState.perChannelSettings) {
        newState.perChannelSettings[channel].color =
          channel === payload ? '#00aeef' : 'black';
      }
      break;

    default:
      break;
  }

  return newState;
}

export default viewModeReducer;
