// Synchronous pure function that calculates coordinates data.

export default function calculateCoordinates(
  dataSegment,
  signals,
  channelHeight,
  amplitude
) {
  const result = {};

  for (let signalLabel in dataSegment) {
    const data = dataSegment[signalLabel];
    const dataLength = data.length;
    const signalSegment = new Float32Array(dataLength); // TODO should try ints as well

    const signalMeta = signals.find(signal => signal.label === signalLabel);
    const digitalRange = 65535; // TODO TODO TODO signalMeta.digitalMax - signalMeta.digitalMin;
    const scale = channelHeight / digitalRange;
    for (let i = 0; i < dataLength; i++) {
      signalSegment[i] = data[i] * scale * amplitude;
    }

    result[signalLabel] = signalSegment;
  }

  return result;
}
