import React, { Component, PureComponent } from 'react';
import { connect } from 'react-redux';

import { STORE_KEY } from '../../constants';
import {
  softSetCurrentTime,
  selectAnnotation,
  modifyAnnotation,
  deleteAnnotation,
} from '../../actions';
import {
  getSortedAnnotations,
  getSelectedAnnotation,
  getSecondWidth,
} from '../../selectors';

function roundSec(duration) {
  return Math.round(duration * 1000000) / 1000000;
}
function roundMs(duration) {
  return Math.round(duration * 1000) / 1000;
}

// This component is about presentation (html structure/style) and mouse resize.
class SelectedAnnotation extends PureComponent {
  constructor(props) {
    super(props);

    this.selectedRef = React.createRef();

    const { time, duration, name, secondWidth } = props;

    this.state = {
      startX: time * secondWidth,
      width: duration * secondWidth,
      name,
      dragging: null,
    };
  }

  componentDidUpdate(prevProps) {
    if (prevProps === this.props) {
      return;
    }

    const { time, duration, name, secondWidth } = this.props;

    this.setState({
      startX: time * secondWidth,
      width: duration * secondWidth,
      name,
      dragging: null,
    });
  }

  startDraggingWest = e => {
    this.startDragging(e, 'w');
  };

  startDraggingEast = e => {
    this.startDragging(e, 'e');
  };

  startDragging(e, direction) {
    this.setState(
      {
        dragging: {
          direction,
          savedPageX: e.pageX,
          savedStartX: this.state.startX,
          savedWidth: this.state.width,
        },
      },
      () => {
        // Not document.body , because body can fill less than 100% height.
        document.documentElement.classList.add(
          'cursor' + direction.toUpperCase()
        );
        document.documentElement.addEventListener(
          'mousemove',
          this.drag,
          false
        );
        document.documentElement.addEventListener(
          'mouseup',
          this.stopDragging,
          false
        );
      }
    );
  }

  drag = e => {
    const {
      dragging: { direction, savedPageX, savedStartX, savedWidth },
    } = this.state;

    const delta = e.pageX - savedPageX;

    let deltaRestricted;
    switch (direction) {
      case 'e':
        deltaRestricted = Math.max(delta, -savedWidth);
        this.setState({ width: savedWidth + deltaRestricted });
        break;

      case 'w':
        deltaRestricted = Math.min(delta, savedWidth);
        this.setState({
          startX: savedStartX + deltaRestricted,
          width: savedWidth - deltaRestricted,
        });
        break;

      default:
        break;
    }
  };

  stopDragging = e => {
    document.documentElement.classList.remove('cursorW');
    document.documentElement.classList.remove('cursorE');
    document.documentElement.removeEventListener('mousemove', this.drag, false);
    document.documentElement.removeEventListener(
      'mouseup',
      this.stopDragging,
      false
    );
    const { startX, width } = this.state;
    const { secondWidth, onChange } = this.props;

    this.setState({ dragging: null }, function() {
      onChange({
        time: startX / secondWidth,
        duration: roundSec(width / secondWidth),
      });
    });
  };

  componentDidMount() {
    // It's not elegant to put focusing behaviour here, but I cannot come up
    // with a better solution now.
    this.selectedRef.current.focus();
  }

  componentWillUnmount() {
    document.documentElement.classList.remove('cursorW');
    document.documentElement.classList.remove('cursorE');
    document.documentElement.removeEventListener('mousemove', this.drag, false);
    document.documentElement.removeEventListener(
      'mouseup',
      this.stopDragging,
      false
    );
  }

  render() {
    const { secondWidth, onKeyDown, onBlur } = this.props;
    const { startX, width, name } = this.state;

    const containerWidth = Math.max(width, 2); // One border width.

    const duration = width / secondWidth;
    const durationLabel =
      duration < 1 ? roundMs(duration * 1000) + 'ms' : roundSec(duration) + 's';

    return (
      <div
        tabIndex="0"
        ref={this.selectedRef}
        onKeyDown={onKeyDown}
        className="selectedAnnotation"
        style={{
          transform: `translateX(${startX}px)`,
          width: containerWidth,
        }}
      >
        <div className="leftBorder" onMouseDown={this.startDraggingWest} />
        <div className="rightBorder" onMouseDown={this.startDraggingEast} />
        <div className="annotationDuration">
          <div className="measureLine" />
          <div className="leftGapper" />
          <div className="durationLabel">{durationLabel}</div>
        </div>
        <div className="annotationLabel">{name}</div>
      </div>
    );
  }
}

// This wrapper is about keyboard control.
class SelectedAnnotationWrapper extends Component {
  handleKeyDown = e => {
    const { sortedAnnotations, selected, dispatch } = this.props;
    const { key } = e;
    let idx;

    switch (key) {
      case 'Backspace':
      case 'Delete':
        dispatch(deleteAnnotation(selected.id));
      // No `break` here - this way we jump to next annotation on deleting.

      case 'ArrowDown':
        idx =
          sortedAnnotations.findIndex(
            annotation => annotation.id === selected.id
          ) + 1;
        idx = idx < sortedAnnotations.length ? idx : 0;
        dispatch(selectAnnotation(sortedAnnotations[idx].id));
        dispatch(softSetCurrentTime(sortedAnnotations[idx].time - 0.2));
        break;

      case 'ArrowUp':
        idx =
          sortedAnnotations.findIndex(
            annotation => annotation.id === selected.id
          ) - 1;
        idx = idx >= 0 ? idx : sortedAnnotations.length - 1;
        dispatch(selectAnnotation(sortedAnnotations[idx].id));
        dispatch(softSetCurrentTime(sortedAnnotations[idx].time - 0.2));
        break;

      default:
        break;
    }
  };

  handleChange = annotationData => {
    this.props.dispatch(
      modifyAnnotation({
        ...this.props.selected,
        ...annotationData,
      })
    );
  };

  render() {
    const { selected, secondWidth } = this.props;

    if (!selected) {
      return null;
    }

    // Single child is keyed to re-draw on annotation change.
    return (
      <SelectedAnnotation
        key={selected.id}
        time={selected.time}
        duration={selected.duration}
        name={selected.name}
        secondWidth={secondWidth}
        onKeyDown={this.handleKeyDown}
        onChange={this.handleChange}
      />
    );
  }
}

const mapStateToProps = state => ({
  sortedAnnotations: getSortedAnnotations(state),
  selected: getSelectedAnnotation(state),
  secondWidth: getSecondWidth(state),
});

export default connect(mapStateToProps, undefined, undefined, {
  storeKey: STORE_KEY,
})(SelectedAnnotationWrapper);
