export const SET_ANNOTATIONS = Symbol('SET_ANNOTATIONS');
export const CREATE_ANNOTATION = Symbol('CREATE_ANNOTATION');
export const SELECT_ANNOTATION = Symbol('SELECT_ANNOTATION');
export const MODIFY_ANNOTATION = Symbol('MODIFY_ANNOTATION');
export const DELETE_ANNOTATION = Symbol('DELETE_ANNOTATION');

function checkAnnotation(annotation) {
  if (
    !(
      'id' in annotation &&
      'name' in annotation &&
      'time' in annotation &&
      'duration' in annotation
    )
  ) {
    console.error('Invalid annotations!');
  }
}

export function setAnnotations(annotationsList) {
  if (process.env.NODE_ENV === 'development') {
    for (let annotation of annotationsList) {
      checkAnnotation(annotation);
    }
  }

  return {
    type: SET_ANNOTATIONS,
    payload: annotationsList,
  };
}

export function createAnnotation(annotation) {
  if (process.env.NODE_ENV === 'development') {
    checkAnnotation(annotation);
  }

  return {
    type: CREATE_ANNOTATION,
    payload: annotation,
  };
}

export function selectAnnotation(annotationId) {
  return {
    type: SELECT_ANNOTATION,
    payload: annotationId,
  };
}

export function modifyAnnotation(annotation) {
  if (process.env.NODE_ENV === 'development') {
    checkAnnotation(annotation);
  }

  return {
    type: MODIFY_ANNOTATION,
    payload: annotation,
  };
}

export function deleteAnnotation(annotationId) {
  return {
    type: DELETE_ANNOTATION,
    payload: annotationId,
  };
}

// Define what should escape rollup tree-shaking, since we package preserved
// file structure to npm.
//
// NODE_ENV condition is for consumer app: it should be able to tree-shake
// whatever it wants.
if (process.env.NODE_ENV === 'development') {
  const externalAPI = {
    SET_ANNOTATIONS,
    CREATE_ANNOTATION,
    SELECT_ANNOTATION,
    MODIFY_ANNOTATION,
    DELETE_ANNOTATION,

    setAnnotations,
    createAnnotation,
    selectAnnotation,
    modifyAnnotation,
    deleteAnnotation,
  };

  window &&
    window['This function does not exits'] &&
    window['This function does not exits'](externalAPI);
}
