import { createSelector } from 'reselect';
import { SEGMENT_DURATION } from '../constants';
import { getCurrentSegmentRange } from './navigation';

export const getVisibleAnnotations = createSelector(
  state => state.annotations.list,
  getCurrentSegmentRange,
  (annotationsList, segmentRange) => {
    const rangeStart = SEGMENT_DURATION * segmentRange[0];
    const rangeEnd =
      SEGMENT_DURATION * segmentRange[segmentRange.length - 1] +
      SEGMENT_DURATION;

    return annotationsList.filter(annotation => {
      const { time, duration } = annotation;
      return time < rangeEnd || time + duration > rangeStart;
    });
  }
);

export const getVisibleAnnotationsWithCoordinates = createSelector(
  state => state.presentation.scrollerWidth / state.navigation.epochSize,
  getVisibleAnnotations,
  (secondWidth, visibleAnnotations) =>
    visibleAnnotations.map(annotation => ({
      annotation,
      startX: annotation.time * secondWidth,
      width: annotation.duration * secondWidth,
    }))
);

export const getSelectedAnnotation = createSelector(
  state => state.annotations.list,
  state => state.annotations.selected,
  (annotationsList, selected) =>
    annotationsList.find(annotation => annotation.id === selected)
);

export const getSortedAnnotations = createSelector(
  state => state.annotations.list,
  annotationsList =>
    annotationsList.sort(function(a, b) {
      return a.time - b.time;
    })
);
