import { createSelector } from 'reselect';
import { PRELOAD_SEGMENTS, SEGMENT_DURATION } from '../constants';

export const getCurrentPosition = createSelector(
  state => state.navigation.currentTime,
  state => state.presentation.scrollerWidth,
  state => state.navigation.epochSize,
  (currentTime, scrollerWidth, epochSize) =>
    scrollerWidth * currentTime / epochSize
);

export const getCurrentSegment = createSelector(
  state => state.navigation.currentTime,
  currentTime => Math.floor(currentTime / SEGMENT_DURATION)
);

export const getCurrentSegmentRange = createSelector(
  state => state.header,
  getCurrentSegment,
  (header, currentSegment) => {
    const result = [];

    const { dataRecordsNumber = 0, dataRecordDuration = 0 } = header;
    const startSegment = Math.max(
      0,
      currentSegment - Math.trunc(PRELOAD_SEGMENTS / 2)
    );
    // maxSegment and endSegment are inclusive.
    const maxSegment = Math.ceil(
      dataRecordsNumber * dataRecordDuration / SEGMENT_DURATION
    );
    const endSegment = Math.min(
      maxSegment,
      startSegment + PRELOAD_SEGMENTS - 1
    );

    for (let i = startSegment; i <= endSegment; i++) {
      result.push(i);
    }

    return result;
  }
);

export const getFileDuration = createSelector(
  state => state.header.dataRecordsNumber,
  state => state.header.dataRecordDuration,
  (dataRecordsNumber = 0, dataRecordDuration = 0) =>
    dataRecordsNumber * dataRecordDuration
);

export const getScrollerContentWidth = createSelector(
  getFileDuration,
  state => state.presentation.scrollerWidth,
  state => state.navigation.epochSize,
  (fileDuration, scrollerWidth, epochSize) =>
    fileDuration * scrollerWidth / epochSize
);

export const getSecondWidth = createSelector(
  state => state.navigation.epochSize,
  state => state.presentation.scrollerWidth,
  (epochSize, scrollerWidth) => scrollerWidth / epochSize
);

export const getSegmentWidth = createSelector(
  getSecondWidth,
  secondWidth => SEGMENT_DURATION * secondWidth
);
