import {
  SET_ANNOTATIONS,
  CREATE_ANNOTATION,
  //SELECT_ANNOTATION,
  MODIFY_ANNOTATION,
  DELETE_ANNOTATION,
} from '../actions/annotations';

import {
  ANNOTATION_HISTORY_UNDO,
  ANNOTATION_HISTORY_REDO,
} from '../actions/annotationHistory';

const initialState = {
  log: [],
  index: -1,
};

export function annotationHistoryNoopReducer(state = initialState) {
  return state;
}

// Full-state reducer
export function annotationHistoryReducer(state, action) {
  let newState = state;
  let annotationsCopy;
  const { type } = action;

  const { annotationHistory } = state;
  const { log, index } = annotationHistory;

  switch (type) {
    case SET_ANNOTATIONS:
    case CREATE_ANNOTATION:
    //case SELECT_ANNOTATION:
    case MODIFY_ANNOTATION:
    case DELETE_ANNOTATION:
      annotationsCopy = {
        list: state.annotations.list.slice(),
        selected: state.annotations.selected,
      };

      newState = {
        ...state,
        annotationHistory: {
          log: log.slice(0, index + 1).concat(annotationsCopy),
          index: index + 1,
        },
      };
      break;

    case ANNOTATION_HISTORY_UNDO:
      if (index <= 0) {
        break;
      }

      annotationsCopy = log[index - 1];

      newState = {
        ...state,
        annotations: annotationsCopy,
        annotationHistory: {
          log,
          index: index - 1,
        },
      };
      break;

    case ANNOTATION_HISTORY_REDO:
      if (index >= log.length - 1) {
        break;
      }

      annotationsCopy = log[index + 1];

      newState = {
        ...state,
        annotations: annotationsCopy,
        annotationHistory: {
          log,
          index: index + 1,
        },
      };
      break;

    default:
      break;
  }

  return newState;
}

export default annotationHistoryReducer;
