// Synchronous pure function that calculates derived data.

// TODO code copypasted from `selectors/viewMode.js`. Solve imports for webworkers!
const standardChannelNames = [
  'fp1',
  'fp2',
  'f3',
  'f4',
  'c3',
  'c4',
  'p3',
  'p4',
  'o1',
  'o2',
  'f7',
  'f8',
  't3',
  't4',
  't5',
  't6',
  'a1',
  'a2',
  'fz',
  'cz',
  'pz',
  'f9',
  'f10',
  't9',
  't10',
  'p9',
  'p10',
  'ecg',
];

function normalizeLabel(label) {
  if (standardChannelNames.includes(label.toLowerCase())) {
    return label.toLowerCase();
  }

  const labelChunks = label
    .replace('.', '')
    .replace('-AA', '')
    .replace(' - AA', '')
    .split(' ');
  return (labelChunks[labelChunks.length - 1] || label).toLowerCase();
}

export default function calculateCoordinates(
  dataSegment,
  signals,
  derivedChannels
) {
  const result = {};

  let cursor = 0;
  signals.forEach(function(signal, i) {
    const signalSegment = new Int16Array(signal.sampleRate * 30);
    for (let j = 0; j < 30; j++) {
      const dataRecord = dataSegment[j];
      if (!dataRecord) {
        if (process.env.NODE_ENV === 'development') {
          console.warn(
            `No dataRecord found at index ${j}; probably last segment.`
          );
        }
        break;
      }
      signalSegment.set(
        dataRecord.subarray(cursor, cursor + signal.sampleRate),
        j * signal.sampleRate
      );
    }

    result[signal.label] = signalSegment;
    cursor += signal.sampleRate;
  });

  for (let derivedChannel of derivedChannels) {
    const [ch1, ch2] = derivedChannel.split('-');
    const signal1 = signals.find(({ label }) => normalizeLabel(label) === ch1);
    const signal2 = signals.find(({ label }) => normalizeLabel(label) === ch2);
    if (!signal1 || !signal2) {
      console.error(`Channels not found! ${ch1} , ${ch2}`);
      result[derivedChannel] = new Int16Array(256 * 30); // Dummy
      continue;
    }
    if (signal1.sampleRate !== signal2.sampleRate) {
      throw new Error(`Sample rates do not match! ${ch1} , ${ch2}`);
    }

    const signalSegment = new Int16Array(signal1.sampleRate * 30);
    const dataLength = signalSegment.length;
    for (let i = 0; i < dataLength; i++) {
      signalSegment[i] = result[signal1.label][i] - result[signal2.label][i];
    }
    result[derivedChannel] = signalSegment;
  }

  return result;
}
