import EdfHeader from './edfHeader';

export default class Edf {
  constructor(stream, header = null) {
    this.stream = stream;

    const headerPromise = header
      ? Promise.resolve(header)
      : new EdfHeader(stream);

    return headerPromise.then(header => {
      this.header = header;
      return Promise.resolve(this);
    });
  }

  readDataRecords(callback) {
    const dataRecordSize =
      this.header.signals.reduce((acc, v) => acc + v.sampleRate, 0) * 2;

    let fuse = 0;

    const handle = ({ done, value }) => {
      if (done) {
        callback(null);
        return;
      }
      fuse += 1;
      if (fuse > 1000000) throw new Error('Too long EEG');

      callback(new Int16Array(value.buffer));
      return this.stream.read(dataRecordSize).then(handle);
    };

    return this.stream.read(dataRecordSize).then(handle);
  }
}
