import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { STORE_KEY } from '../../constants';
import { getCurrentPosition, getSecondWidth } from '../../selectors';

class Cursor extends PureComponent {
  constructor(props) {
    super(props);

    this.state = {
      xCord: this.props.currentPosition || 0,
    };
  }

  componentDidMount() {
    document.documentElement.addEventListener(
      'mousemove',
      this._onMouseMove,
      false
    );
  }

  componentWillUnmount() {
    document.documentElement.removeEventListener(
      'mousemove',
      this._onMouseMove,
      false
    );
  }

  _onMouseMove = e => {
    const { scrollerXPosition } = this.props;

    this.setState({ xCord: e.pageX - scrollerXPosition });
  };

  parseTime = time => {
    const hours = String(Math.trunc((time / 3600) % 24)).padStart(2, '0');
    const minutes = String(Math.trunc((time / 60) % 60)).padStart(2, '0');
    const seconds = String(Math.trunc(time % 60)).padStart(2, '0');
    const day = Math.trunc(time / (3600 * 24));

    return (day > 0 ? 'd' + day + ' ' : '') + `${hours}:${minutes}:${seconds}`;
  };

  render() {
    const { currentPosition, secondWidth } = this.props;
    const { xCord } = this.state;

    let cursorTime = this.parseTime((currentPosition + xCord) / secondWidth);

    return (
      <div
        className="cursor"
        style={{
          transform: `translateX(${currentPosition + xCord}px)`,
        }}
      >
        <span className="cursorTime">{cursorTime}</span>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  scrollerXPosition: state.presentation.scrollerXPosition,
  currentPosition: getCurrentPosition(state),
  secondWidth: getSecondWidth(state),
});

export default connect(mapStateToProps, undefined, undefined, {
  storeKey: STORE_KEY,
})(Cursor);
