export default class ArrBuffer {
  constructor(buffer) {
    this.buffer = buffer;
    this.cursor = 0;
  }

  read(size) {
    return this._readFromBuffer(size);
  }

  _readFromBuffer(size) {
    const done = this.buffer.length <= this.cursor;
    if (this.buffer.length < this.cursor) {
      console.warn(
        'Trying to read past data boundary; source file may be corrupted.'
      );
    }
    if (done) {
      this.buffer = null;
      this.cursor = null;
      return Promise.resolve({ done, value: null });
    }

    const end = this.cursor + size;
    const value = this.buffer.slice(this.cursor, end);
    this.cursor = end;
    return Promise.resolve({ done, value });
  }
}
