const HEADER_SPEC = {
  version: { size: 8, type: 'int' },
  patientId: { size: 80, type: 'string' },
  recordingId: { size: 80, type: 'string' },
  recordingStartDate: { size: 8, type: 'string' },
  recordingStartTime: { size: 8, type: 'string' },
  headerSize: { size: 8, type: 'int' },
  reserved: { size: 44, type: 'binary' },
  dataRecordsNumber: { size: 8, type: 'int' },
  dataRecordDuration: { size: 8, type: 'float' },
  signalsNumber: { size: 4, type: 'int' },
};

const SIGNAL_SPEC = {
  label: { size: 16, type: 'string' },
  transducerType: { size: 80, type: 'string' },
  physicalDimension: { size: 8, type: 'string' },
  physicalMin: { size: 8, type: 'float' },
  physicalMax: { size: 8, type: 'float' },
  digitalMin: { size: 8, type: 'int' },
  digitalMax: { size: 8, type: 'int' },
  prefiltering: { size: 80, type: 'string' },
  sampleRate: { size: 8, type: 'int' },
  reserved: { size: 32, type: 'string' },
};

export default class EdfHeader {
  constructor(stream) {
    return stream.read(256).then(({ value }) => {
      const header = this._parseHeader(value);
      EdfHeader._validateHeader(header);
      const signalsNumber = header.signalsNumber;

      return stream.read(256 * signalsNumber).then(({ value }) => {
        header.signals = this._parseSignals(value, signalsNumber);
        return new Promise(resolve => resolve(header));
      });
    });
  }

  _parseSignals(bytes, signalsNumber) {
    let result = [];

    for (let signalId = 0; signalId < signalsNumber; signalId++) {
      const signal = this._parseSignal(bytes, signalId, signalsNumber);
      EdfHeader._validateSignal(signal);
      result.push(signal);
    }

    return result;
  }

  _parseHeader(bytes) {
    let result = {};
    let offset = 0;

    for (const field in HEADER_SPEC) {
      const fieldSpec = HEADER_SPEC[field];
      const fieldBytes = bytes.slice(offset, offset + fieldSpec.size);
      result[field] = this._castValue(fieldBytes, fieldSpec.type);
      offset += fieldSpec.size;
    }

    return result;
  }

  _parseSignal(bytes, signalId, signalsNumber) {
    let result = {};
    let offset = 0;

    for (const field in SIGNAL_SPEC) {
      const fieldSpec = SIGNAL_SPEC[field];
      const start = offset + signalId * fieldSpec.size;
      const fieldBytes = bytes.slice(start, start + fieldSpec.size);
      result[field] = this._castValue(fieldBytes, fieldSpec.type);
      offset += signalsNumber * fieldSpec.size;
    }

    return result;
  }

  _castValue(bytes, type) {
    const str = new TextDecoder('utf-8').decode(bytes.buffer).trim();

    switch (type) {
      case 'int':
        return parseInt(str);
      case 'float':
        return parseFloat(str);
      default:
        return str;
    }
  }

  static _validateHeader(header) {
    if (header.version !== 0) throw 'Unsupported EDF version';

    if (
      header.dataRecordsNumber === undefined ||
      header.dataRecordsNumber < 1 ||
      header.dataRecordsNumber > 1000000
    )
      throw 'Wrong records number';

    if (
      header.dataRecordDuration === undefined ||
      header.dataRecordDuration <= 0 ||
      header.dataRecordDuration > 1000
    )
      throw 'Too long record duration';

    if (
      header.signalsNumber === undefined ||
      header.signalsNumber < 1 ||
      header.signalsNumber > 256
    )
      throw 'Wrong signals number';
  }

  static _validateSignal(signal) {
    if (
      signal.sampleRate === undefined ||
      signal.sampleRate < 1 ||
      signal.sampleRate > 100000
    )
      throw 'Wrong sample rate';

    if (signal.physicalMin === undefined) throw 'Wrong physical minimum';
    if (signal.physicalMax === undefined) throw 'Wrong physical maximum';

    if (signal.digitalMin === undefined) throw 'Wrong digital minimum';
    if (signal.digitalMax === undefined) throw 'Wrong digital maximum';
  }
}
