import { combineReducers } from 'redux';
import navigationReducer from './navigation';
import presentationReducer from './presentation';
import headerReducer from './header';
import dataReducer from './data';
import viewModeReducer from './viewMode';
import annotationsReducer from './annotations';
import annotationHistoryReducer, {
  annotationHistoryNoopReducer,
} from './annotationHistory';

import {
  ADD_DATA_SEGMENT_30S,
  ADD_DERIVED_SEGMENT_30S,
  ADD_COORDINATES_SEGMENT_30S,
  SET_SCROLLER_PARAMS,
  SET_VIEWPORT_PARAMS,
  SET_VISIBLE_CHANNELS,
  SET_INPUT_MONTAGE,
  SET_BIPOLAR_MONTAGE,
  SET_DEFAULT_AMPLITUDE,
} from '../actions';

import { getDerivedChannels, getFileDuration } from '../selectors';

import calculateCoordinates from '../workers/calculateCoordinates';
import calculateDerived from '../workers/calculateDerived';

const regularReducer = combineReducers({
  navigation: navigationReducer,
  presentation: presentationReducer,
  header: headerReducer,
  data: dataReducer,
  viewMode: viewModeReducer,
  annotations: annotationsReducer,
  annotationHistory: annotationHistoryNoopReducer,
});

// TODO develop selectors to determine if recalc is needed.

// This full-state reducer manages dependencies for `data` slice of store. It
// does so in a synchronous manner.
function dataDependencyReducer(state, action) {
  const { type, payload } = action;

  switch (type) {
    case ADD_DATA_SEGMENT_30S:
      const newState = { ...state };
      newState.data = { ...newState.data };

      const rawSegment = payload.data;
      const derivedSegment = calculateDerived(
        rawSegment,
        state.header.signals,
        getDerivedChannels(state)
      );
      const coordinatesSegment = calculateCoordinates(
        derivedSegment,
        state.header.signals,
        state.presentation.viewportHeight /
          state.viewMode.visibleChannels.length,
        state.viewMode.channelDefaults.amplitude //TODO TODO
      );

      newState.data.derivedValues[payload.index] = derivedSegment;
      newState.data.onScreenCoordinates[payload.index] = coordinatesSegment;

      return newState;

    case ADD_DERIVED_SEGMENT_30S: {
      const newState = { ...state };
      newState.data = { ...newState.data };

      const derivedSegment = payload.data;
      const coordinatesSegment = calculateCoordinates(
        derivedSegment,
        state.header.signals,
        state.presentation.viewportHeight /
          state.viewMode.visibleChannels.length,
        state.viewMode.channelDefaults.amplitude //TODO TODO
      );

      newState.data.onScreenCoordinates[payload.index] = coordinatesSegment;

      return newState;
    }

    case SET_VISIBLE_CHANNELS:
    case SET_INPUT_MONTAGE:
    case SET_BIPOLAR_MONTAGE:
    case SET_DEFAULT_AMPLITUDE:
    case SET_SCROLLER_PARAMS:
    case SET_VIEWPORT_PARAMS: {
      // TODO Also - signals, viewportHeight, amplitude...
      const newState = { ...state };
      newState.data = { ...newState.data };

      for (let i = 0; i < newState.data.onScreenCoordinates.length; i++) {
        if (!newState.data.onScreenCoordinates[i]) {
          continue;
        }

        const derivedSegment = newState.data.derivedValues[i];
        const coordinatesSegment = calculateCoordinates(
          derivedSegment,
          state.header.signals,
          state.presentation.viewportHeight /
            state.viewMode.visibleChannels.length,
          state.viewMode.channelDefaults.amplitude //TODO TODO
        );

        newState.data.onScreenCoordinates[i] = coordinatesSegment;
      }

      return newState;
    }

    case ADD_COORDINATES_SEGMENT_30S:
      // Do nothing; coordinates data added in regularReducer, and no other data
      // depends on it.
      return state;

    default:
      return state;
  }
}

// This default reducer combines data deps calculation and other, non-related
// complex actions like guarding against out-of-bounds navigation.
function rootReducer(state, action) {
  const regularState = regularReducer(state, action);
  const annotationHistoryState = annotationHistoryReducer(regularState, action);
  const dataDependenciesState = dataDependencyReducer(
    annotationHistoryState,
    action
  );

  const { navigation } = dataDependenciesState;
  const maxTime = getFileDuration(dataDependenciesState) - navigation.epochSize;
  navigation.currentTime = Math.max(
    0,
    Math.min(navigation.currentTime, maxTime)
  );

  return dataDependenciesState;
}

export { rootReducer, regularReducer };
export default rootReducer;
