import React, { PureComponent } from 'react';
import { connect } from 'react-redux';

import { STORE_KEY } from '../../constants';
import { getCurrentPosition, getFileDuration } from '../../selectors';

import {
  setCurrentTime,
  shiftCurrentTime,
  setScrollerParams,
} from '../../actions';

class TransformScroller extends PureComponent {
  componentDidMount() {
    const { dispatch, navigation: { currentTime } } = this.props;

    this.resizeHandler();
    dispatch(setCurrentTime(currentTime));

    window.addEventListener('resize', this.resizeHandler, false);
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.resizeHandler, false);
  }

  resizeHandler = () => {
    this.props.dispatch(
      setScrollerParams({
        scrollerWidth: this.scroller.offsetWidth,
        scrollerXPosition: this.scroller.getBoundingClientRect().x,
      })
    );
  };

  scrollHandler = e => {
    const {
      navigation: { epochSize },
      presentation: { scrollerWidth },
    } = this.props;

    let delta = e.deltaX || e.deltaY;

    if (e.deltaMode === 1) {
      // DOM_DELTA_LINE mode of scrolling; currently only Firefox does that
      // see https://github.com/cubiq/iscroll/issues/577#issuecomment-49095332

      delta *= 20; // Just rule-of-thumb value.
    }

    let direction = -1;

    const macosPlatforms = ['Macintosh', 'MacIntel', 'MacPPC', 'Mac68K'];
    if (macosPlatforms.includes(window.navigator.platform)) {
      direction = 1;
    }

    const time = direction * delta * epochSize / scrollerWidth;

    this.props.dispatch(shiftCurrentTime(time));
  };

  onKeyDown = e => {
    const { epochSize } = this.props.navigation;

    let direction = null;
    if (e.key === 'ArrowRight') {
      direction = 1;
    } else if (e.key === 'ArrowLeft') {
      direction = -1;
    }
    if (direction === null) return;

    let duration = epochSize;
    if (e.shiftKey) duration = 10;
    if (e.ctrlKey) duration = 1;
    if (e.shiftKey && e.ctrlKey) duration = 30;

    this.props.dispatch(shiftCurrentTime(duration * direction));
  };

  onMouseOver = () =>
    this.scroller.matches(':focus-within') || this.scroller.focus();

  render() {
    return (
      <div
        className="scroller  scroller__transform"
        onWheel={this.scrollHandler}
        onMouseOver={this.onMouseOver}
        ref={el => (this.scroller = el)}
        tabIndex="0"
        onKeyDown={this.onKeyDown}
      >
        <div
          style={{
            transform: `translateX(${-this.props.currentPosition}px)`,
          }}
        >
          {this.props.children}
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  ...state,
  currentPosition: getCurrentPosition(state),
  fileDuration: getFileDuration(state),
});

export default connect(mapStateToProps, undefined, undefined, {
  storeKey: STORE_KEY,
})(TransformScroller);
