import {
  SET_ANNOTATIONS,
  CREATE_ANNOTATION,
  SELECT_ANNOTATION,
  MODIFY_ANNOTATION,
  DELETE_ANNOTATION,
} from '../actions';

const initialState = {
  list: [], // { id, time, duration, name } and optionally { cssClass } ( used in minimap too )
  selected: null, // Just an id. Use selector `getVisibleAnnotations`
};

export function annotationsReducer(state = initialState, action) {
  let newState = state;
  const { type, payload } = action;

  switch (type) {
    case SET_ANNOTATIONS:
      newState = {
        ...state,
        list: payload,
      };
      // New list? Remove selection.
      if (payload !== state.list) {
        newState.selected = null;
      }
      break;

    case CREATE_ANNOTATION:
      if (process.env.NODE_ENV === 'development') {
        if (state.list.find(annotation => annotation.id === payload.id)) {
          console.warn(
            'Trying to create annotation with same id as one of existing annotations!'
          );
        }
      }

      newState = {
        ...state,
        list: state.list.concat(payload),
      };
      break;

    case SELECT_ANNOTATION:
      newState = {
        ...state,
        selected: payload,
      };
      break;

    case MODIFY_ANNOTATION:
      if (process.env.NODE_ENV === 'development') {
        if (!state.list.find(annotation => annotation.id === payload.id)) {
          console.warn('Trying to modify non-existent annotation!');
        }
      }

      newState = {
        ...state,
        list: state.list.map(
          annotation => (annotation.id === payload.id ? payload : annotation)
        ),
      };
      break;

    case DELETE_ANNOTATION:
      if (process.env.NODE_ENV === 'development') {
        if (!state.list.find(annotation => annotation.id === payload)) {
          console.warn('Trying to delete non-existent annotation!');
        }
      }

      newState = {
        ...state,
        list: state.list.filter(annotation => annotation.id !== payload),
      };
      break;

    default:
      break;
  }

  return newState;
}

export default annotationsReducer;
