export default class FunnelBuffer {
  constructor(stream) {
    this.stream = stream;
    this.buffer = new Uint8Array();
  }

  read(size) {
    if (size > this.buffer.length) {
      return this._readFromStream(size, 0);
    } else {
      return this._readFromBuffer(size);
    }
  }

  _readFromStream(size, fuse) {
    if (fuse > 1000) throw 'Recursion level too deep';

    return this.stream.read().then(({ done, value }) => {
      if (done)
        return new Promise(resolve => resolve({ done: true, value: null }));
      this._pushToBuffer(value);
      if (this.buffer.length < size)
        return this._readFromStream(size, fuse + 1);
      return this._readFromBuffer(size);
    });
  }

  _pushToBuffer(values) {
    let newBuffer = new Uint8Array(this.buffer.length + values.length);
    newBuffer.set(this.buffer, 0);
    newBuffer.set(values, this.buffer.length);
    this.buffer = newBuffer;
  }

  _readFromBuffer(size) {
    const value = this.buffer.slice(0, size);
    this.buffer = this.buffer.slice(size);
    return new Promise(resolve => resolve({ done: false, value: value }));
  }
}
