import React, { PureComponent } from 'react';

class Segment extends PureComponent {
  constructor(props) {
    super(props);

    this.canvasRef = React.createRef();
  }

  componentDidMount() {
    this.canvasRender();
  }

  componentDidUpdate() {
    this.canvasRender();
  }

  canvasRender() {
    const { visibleChannels, segment30s, stepWidthOptions } = this.props;

    if (!segment30s) {
      return;
    }

    const { height } = this.props;
    const signalHeight = height / visibleChannels.length;
    const canvas = this.canvasRef.current;
    const ctx = canvas.getContext('2d');

    ctx.clearRect(0, 0, canvas.width, canvas.height);

    let currentSignal = 0;

    for (let signalMeta of visibleChannels) {
      if (signalMeta.invalid) {
        currentSignal++;
        continue;
      }

      let signal =
        signalMeta.type === 'derived'
          ? signalMeta.signals[0]
          : signalMeta.signal; // TODO

      let signalStartY = signalHeight * currentSignal + signalHeight / 2;

      ctx.beginPath();
      ctx.moveTo(0, signalStartY);

      const data = segment30s[signal.label];
      const dataLength = data.length;
      const stepWidthOption = stepWidthOptions[signal.sampleRate];

      for (let j = 0; j < dataLength; j++) {
        ctx.lineTo(stepWidthOption[j], signalStartY + data[j]);
      }

      ctx.moveTo(0, signalStartY);
      ctx.closePath();
      if (signalMeta.color && signalMeta.color !== 'black') {
        ctx.strokeStyle = signalMeta.color;
        ctx.stroke();
        ctx.strokeStyle = '#000000';
      } else {
        ctx.stroke();
      }

      currentSignal++;
    }
  }

  render() {
    const { segWidth, height } = this.props;

    return (
      <canvas
        className="plotCanvas"
        ref={this.canvasRef}
        width={segWidth}
        height={height}
      />
    );
  }
}

export default Segment;
