import { createSelector } from 'reselect';
import { getSecondWidth } from './index';
import { SEGMENT_DURATION } from '../constants';

const bipolarNames = [
  'fp1-f7',
  'f7-t3',
  't3-t5',
  't5-o1',
  'fp1-f3',
  'f3-c3',
  'c3-p3',
  'p3-o1',
  'fz-cz',
  'cz-pz',
  'fp2-f4',
  'f4-c4',
  'c4-p4',
  'p4-o2',
  'fp2-f8',
  'f8-t4',
  't4-t6',
  't6-o2',
];

const standardChannelNames = [
  'fp1',
  'fp2',
  'f3',
  'f4',
  'c3',
  'c4',
  'p3',
  'p4',
  'o1',
  'o2',
  'f7',
  'f8',
  't3',
  't4',
  't5',
  't6',
  'a1',
  'a2',
  'fz',
  'cz',
  'pz',
  'f9',
  'f10',
  't9',
  't10',
  'p9',
  'p10',
  'ecg',
];

function normalizeLabel(label) {
  if (standardChannelNames.includes(label.toLowerCase())) {
    return label.toLowerCase();
  }

  const labelChunks = label
    .replace('.', '')
    .replace('-AA', '')
    .replace(' - AA', '')
    .split(' ');
  return (labelChunks[labelChunks.length - 1] || label).toLowerCase();
}

export const getDerivedChannels = createSelector(
  state => state.viewMode.userSpecifiedChannels,
  userSpecifiedChannels => [...bipolarNames, ...userSpecifiedChannels]
);

export const getChannelSettings = createSelector(
  state => state.header.signals,
  state => state.viewMode.channelDefaults,
  state => state.viewMode.perChannelSettings,
  getDerivedChannels,
  (edfSignals, defaults, channelSettings, derivedChannels) => {
    const result = {};
    if (!edfSignals) {
      return result;
    }

    for (let signal of edfSignals) {
      const { label } = signal;

      if (result[label]) {
        throw new Error(`Duplicate signal ${label}`);
      }

      const normalizedLabel = normalizeLabel(label);

      result[label] = Object.assign({}, defaults, channelSettings[label], {
        normalizedLabel,
        type: 'input',
        signal,
      });
    }

    // For now, we assume only subtraction type of derived channels.
    for (let label of derivedChannels) {
      if (result[label]) {
        throw new Error(`Duplicate signal ${label}`);
      }

      const [ch1, ch2] = label.split('-');
      const signal1 = edfSignals.find(
        ({ label }) => normalizeLabel(label) === ch1
      );
      const signal2 = edfSignals.find(
        ({ label }) => normalizeLabel(label) === ch2
      );

      if (process.env.NODE_ENV === 'development') {
        if (
          !signal1 ||
          !signal2 ||
          signal1.sampleRate !== signal2.sampleRate ||
          signal1.digitalMin !== signal2.digitalMin ||
          signal1.digitalMax !== signal2.digitalMax
        ) {
          console.warn('Invalid derived channel!', label);
        }
      }

      result[label] = Object.assign({}, defaults, channelSettings[label], {
        normalizedLabel: label,
        type: 'derived',
        signals: [signal1, signal2],
        invalid:
          !signal1 ||
          !signal2 ||
          signal1.sampleRate !== signal2.sampleRate ||
          signal1.digitalMin !== signal2.digitalMin ||
          signal1.digitalMax !== signal2.digitalMax,
      });
    }

    return result;
  }
);

export const getVisibleChannels = createSelector(
  state => state.viewMode.visibleChannels,
  getChannelSettings,
  (visibleChannels, channelSettings) =>
    visibleChannels.map(label => channelSettings[label])
);

export const getPossibleChannels = createSelector(
  getChannelSettings,
  channelSettings =>
    Object.keys(channelSettings).filter(
      label => !channelSettings[label].invalid
    )
);

export const getStepWidth = createSelector(
  getVisibleChannels,
  state => state.header.dataRecordDuration,
  getSecondWidth,
  (visibleChannel, dataRecordDuration, secondWidth) => {
    let stepWidth = {};

    for (let channel of visibleChannel) {
      const currentSampleRate = channel.signal.sampleRate;
      let currentStepWidth = secondWidth / currentSampleRate;

      if (!stepWidth[currentSampleRate]) {
        const length =
          SEGMENT_DURATION / dataRecordDuration * currentSampleRate;

        const points = new Array(length).fill(null);

        stepWidth[currentSampleRate] = points.map(
          (item, idx) => idx * currentStepWidth
        );
      }
    }

    return stepWidth;
  }
);
