import deferred from './deferred';

// TODO destruction behaviour +/-

const MAX_CACHE_SIZE = 2000; // dataRecords. TODO tunable
const MAX_REQUEST_SIZE = 500; // dataRecords. TODO tunable

class ExampleCache {
  constructor(resource, header, options = { concurrency: 2 }) {
    if (!resource || !header) {
      throw new Error('resource or header not supplied to cache constructor');
    }

    this.resource = resource;
    this.header = header;
    this.options = options;

    const totalSize = header.dataRecordsNumber;

    // Actual dataRecords in cache
    this._data = new Array(totalSize).fill(null);
    this._dataCount = 0;

    // dataRecord indices that have been requested by user
    // Each request by user is assigned a sequential number, for cache clearing
    // later
    this._requested = new Array(totalSize).fill(null);
    this._lastRequested = 0;

    // dataRecord indices that are being fetched from the server right now
    this._fetching = new Array(totalSize).fill(null);

    this.unsubscribeChunk = resource.on(
      'chunk',
      (index, dataRecord, requestId) => {
        this._processNewChunk(index, dataRecord, requestId);
      }
    );
    this.unsubscribeFinish = resource.on('finish', () => {
      this.scheduleDataRequest();
    });
    this.unsubscribeDestroy = resource.on('destroy', () => {
      this.destroy(false);
    });
  }

  getChunksAsync(start, _size) {
    const totalSize = this.header.dataRecordsNumber;

    if (start < 0 || start > totalSize - 1) {
      // prettier-ignore
      throw new Error(
        `Incorrect start supplied to getChunksAsync(): ${start} (total edf size is ${totalSize})`
      );
    }

    const size = Math.min(_size, totalSize - start);

    if (process.env.NODE_ENV === 'development') {
      if (size < _size) {
        // prettier-ignore
        console.log(
          `getChunksAsync(${start}, ${_size}): requested chunks past edf file boundary (${totalSize}); truncating to getChunksAsync(${start}, ${size}).`
        );
      }
    }

    const requestNumber = ++this._lastRequested;
    let canGetFromCache = true;
    let willFetchEventually = true;
    for (let i = start; i < start + size; i++) {
      this._requested[i] = requestNumber;
      canGetFromCache = canGetFromCache && !!this._data[i];
      willFetchEventually =
        willFetchEventually && (!!this._data[i] || !!this._fetching[i]);
    }

    // Simple case - we've got everything we need in cache...
    if (canGetFromCache) {
      console.info('full cache hit', start, size);
      return deferred().resolve(this._data.slice(start, start + size));
    }

    // ...complex case - we don't have everything.
    const deferredPromise = deferred((res, rej) => {
      let collectedChunks = this._data.slice(start, start + size);
      const unsubscribeChunk = this.resource.on(
        'chunk',
        (index, dataRecord) => {
          if (
            index < start ||
            index > start + size - 1 ||
            !!collectedChunks[index - start]
          ) {
            return;
          }
          collectedChunks[index - start] = dataRecord;

          if (!collectedChunks.includes(null)) {
            // All chunks collected; resolve promise with a shallow copy of array.
            res(collectedChunks.slice());
            destroy();
          }
        }
      );
      // TODO FIXME it's faulty implementation. Must be `this.on('destroy'`, not `this.resource.on('destroy'`
      const unsubscribeDestroy = this.resource.on('destroy', () => {
        destroy();
      });

      // To prevent memory leaks, we should clean up everything in regard to
      // this promise.
      const destroy = () => {
        unsubscribeChunk();
        unsubscribeDestroy();
        for (let i = 0; i < size; i++) {
          collectedChunks[i] = null;
        }
        collectedChunks = null;
      };
    });

    // Are dataRecords that we need being fetched right now? If not, we should
    // ask resource to load data.
    if (!willFetchEventually) {
      this.scheduleDataRequest();
    }

    return deferredPromise;
  }

  scheduleDataRequest() {
    if (this.resource.getRequestCount() >= this.options.concurrency) {
      // Too many requests already running; do nothing. When any running
      // request is finished, this function will be called again.
      return;
    }

    const totalSize = this.header.dataRecordsNumber;
    let start = null;
    let end = null;
    for (let i = 0; i < totalSize; i++) {
      if (!this._requested[i] || !!this._data[i] || !!this._fetching[i]) {
        continue;
      }
      if (start === null) {
        start = i;
      }
      end = i;
    }
    if (start === null) {
      return;
    }

    const size = Math.min(end - start + 1, MAX_REQUEST_SIZE);

    // We may get something other than what we've asked for
    const [requestId, requestStart, requestSize] = this.resource.loadChunks(
      start,
      size
    );
    for (let i = requestStart; i < requestSize; i++) {
      this._fetching[i] = this._fetching[i] || [];
      this._fetching[i].push(requestId);
    }
  }

  _cleanOneChunk() {
    // Simple implementation, clean only one chunk (make full scan for that).
    // TODO better
    const totalSize = this.header.dataRecordsNumber;
    let lowestGeneration = Infinity;
    let found = null;
    for (let i = 0; i < totalSize; i++) {
      if (!this._data[i] || this._requested[i] >= lowestGeneration) {
        continue;
      }
      lowestGeneration = this._requested[i];
      found = i;
    }

    if (found === null) {
      throw new Error(
        'Tried to clean one chunk from cache, but found no eligible chunk!'
      );
    }
    if (this._fetching[found]) {
      throw new Error(
        'Tried to clean one chunk from cache, but this chunk is being requested!'
      );
    }

    this._data[found] = null;
    this._requested[found] = null;
    this._dataCount -= 1;
  }

  _processNewChunk(index, dataRecord, requestId) {
    if (this._dataCount > MAX_CACHE_SIZE) {
      this._cleanOneChunk();
    }

    this._data[index] = dataRecord;
    this._dataCount += 1;

    // For those chunks that are not explicitly requested by user, we set
    // their generation number to zero, so that they are cleaned from cache
    // first.
    this._requested[index] = this._requested[index] || 0;

    if (!this._fetching[index]) {
      // Received chunk out of the blue. Do nothing with _fetching[] array.
    } else if (this._fetching[index].length === 1) {
      this._fetching[index] = null;
    } else {
      const idx = this._fetching[index].indexOf(requestId);
      if (idx === -1) {
        throw new Error("Mustn't happen");
      }
      this._fetching[index].splice(idx, 1);
    }
  }

  destroy(destroyResource = true) {
    this.unsubscribeChunk();
    this.unsubscribeFinish();
    this.unsubscribeDestroy();
    for (let i = 0; i < this._data.length; i++) {
      this._data[i] = null;
    }
    this._data = null;
    this._requested = null;
    this._fetching = null;

    destroyResource && this.resource.destroy();
  }
}

export default ExampleCache;
