// Example resource for both streaming clients (e.g. Chrome) and non-streaming
// clients (e.g. Firefox) with server that supports HTTP Range.

/*
  async API (returns abortable promises):
    fetchHeader
    loadChunks
    loadAll (maybe?)
    getRequestCount

  events API: header, chunk, start, finish, error, destroy
*/

import FunnelBuffer from './edf/funnelBuffer';
import ArrBuffer from './edf/arrBuffer';
import EdfHeader from './edf/edfHeader';
import streamReader from './edf/edfStreamReader';
import deferred from './deferred';

function _defaultFetch(resource, options) {
  return window.fetch(resource, { ...options, credentials: 'same-origin' });
}

class ExampleRangeResource {
  constructor(source, options = {}) {
    this.url = source.url;
    this._fetch =
      typeof options.fetch === 'function' ? options.fetch : _defaultFetch;

    this.header = null;
    this.headerPromise = null;

    this._subscribers = {
      header: [],
      chunk: [],
      start: [],
      finish: [],
      error: [],
      destroy: [],
    };

    this._requestCount = 0;
    this.on('start', () => this._requestCount++);
    this.on('finish', () => this._requestCount--);
  }

  getRequestCount() {
    return this._requestCount;
  }

  fetchHeader() {
    if (!this.headerPromise) {
      this.headerPromise = this._fetchHeader().then(header => {
        this._emit('header', header);
        this.header = header;
        return header;
      });
    }

    return this.headerPromise;
  }

  _fetchHeader() {
    this._emit('start');
    const headerPromise = this._fetch(this.url, {
      headers: new Headers({
        Range: 'bytes=0-16384',
      }),
    })
      .then(response => {
        if (response.status !== 206)
          throw new Error('EEG file range unavailable');

        if (
          'ReadableStream' in window &&
          response.body instanceof ReadableStream
        ) {
          return response.body;
        } else if (response.arrayBuffer) {
          return response.arrayBuffer();
        } else {
          throw new Error('Unexpected response!');
        }
      })
      .then(source => {
        let stream;
        switch (true) {
          case 'ReadableStream' in window && source instanceof ReadableStream:
            stream = new FunnelBuffer(source.getReader());
            break;
          case source instanceof ArrayBuffer:
            stream = new ArrBuffer(new Uint8Array(source));
            break;
          default:
            throw new Error('Unsupported source for edfStreamReader');
            break;
        }

        return new EdfHeader(stream).then(header => {
          this._emit('finish');
          stream.stream && stream.stream.cancel();
          return header;
        });
      });

    return headerPromise;
  }

  loadChunks(start, size) {
    if (!this.header) {
      throw new Error(
        'Header not fetched yet. Use promise-returning method fetchHeader().'
      );
    }

    const totalSize = this.header.dataRecordsNumber;
    if (start < 0 || start + size - 1 > totalSize) {
      // prettier-ignore
      throw new Error(
        `Incorrect start/size supplied to loadChunks(): ${start}, ${size} (total edf size is ${totalSize})`
      );
    }

    // Start fetching data range.
    const requestId = new Date().getTime();
    this._fetchChunks(requestId, start, size);

    return [requestId, start, size];
  }

  _fetchChunks(requestId, start, size) {
    return new Promise((resolve, reject) => {
      this._emit('start', requestId);
      // TODO cancel stream on destroy somehow.

      const { headerSize, signals } = this.header;

      const dataRecordSize =
        signals.reduce((acc, v) => acc + v.sampleRate, 0) * 2;

      const bytesStart = headerSize + start * dataRecordSize;
      const bytesEnd = headerSize + (start + size) * dataRecordSize - 1; // HTTP Range is inclusive; https://tools.ietf.org/html/rfc7233

      this._fetch(this.url, {
        headers: new Headers({
          Range: `bytes=${bytesStart}-${bytesEnd}`,
        }),
      })
        .then(response => {
          if (response.status !== 206)
            throw new Error('EEG file range unavailable');

          if (
            'ReadableStream' in window &&
            response.body instanceof ReadableStream
          ) {
            return response.body;
          } else if (response.arrayBuffer) {
            return response.arrayBuffer();
          } else {
            throw new Error('Unexpected response!');
          }
        })
        .then(source => {
          return streamReader(
            source,
            (dataRecord, index, total, edfHeader) => {
              // edf's readDataRecords() method does one extra callback with null
              // after finish. We check for index being not greater than requested,
              // to emit 'chunk' events correctly.
              if (index < size) {
                this._emit('chunk', start + index, dataRecord, requestId);
              }

              if (process.env.NODE_ENV === 'development') {
                if (index >= size) {
                  console.info(
                    'ignored: index, start, size',
                    index,
                    start,
                    size
                  );
                }
              }

              if (index === size - 1) {
                this._emit('finish', requestId);
                resolve(index, total);
              }
            },
            this.header
          );
        });
    });
  }

  // Pubsub implementation
  _emit(event, ...data) {
    for (let callback of this._subscribers[event]) {
      callback(...data);
    }
  }

  on(event, callback) {
    if (!this._subscribers[event]) {
      console.warn(`Event ${event} not supported; ignoring.`);
      return false;
    }

    this._subscribers[event].push(callback);
    return () => this.off(event, callback);
  }

  off(event, callback) {
    if (!this._subscribers[event]) {
      console.warn(`Event ${event} not supported; ignoring.`);
      return false;
    }

    const prevLength = this._subscribers[event].length;
    this._subscribers[event] = this._subscribers[event].filter(function(cb) {
      return cb !== callback;
    });

    return prevLength > this._subscribers[event].length;
  }

  // TODO destroy method; more.
  destroy() {
    this._emit('destroy');
  }
}

export default ExampleRangeResource;
