// This component is perhaps only temporary solution to 'create new annotation'
// proplem. Capturing mouse events in scroller viewport should be redesigned.
//
// Not lifting state yet - component not mature enough.

import React, { PureComponent } from 'react';
import { connect } from 'react-redux';

import { STORE_KEY } from '../../constants';
import { createAnnotation, selectAnnotation } from '../../actions';
import {
  getSelectedAnnotation,
  getSecondWidth,
  getCurrentPosition,
} from '../../selectors';

class NewSelection extends PureComponent {
  render() {
    const { savedX, delta } = this.props;

    if (savedX === null) {
      return null;
    }

    const startX = delta >= 0 ? savedX : savedX + delta;
    const width = Math.abs(delta);

    return (
      <div
        className="newSelection"
        style={{ transform: `translateX(${startX}px)`, width }}
      />
    );
  }
}
class CtrlClickCatcher extends PureComponent {
  constructor(props) {
    super(props);

    this.catcherRef = React.createRef();
    this.state = { ctrlPressed: false, savedX: null, delta: null };
  }

  componentDidMount() {
    document.documentElement.addEventListener(
      'contextmenu',
      this.preventContextMenu,
      false
    );
    document.documentElement.addEventListener('keydown', this.onKeyDown, false);
    document.documentElement.addEventListener('keyup', this.onKeyUp, false);
  }

  componentWillUnmount() {
    document.documentElement.removeEventListener(
      'contextmenu',
      this.preventContextMenu,
      false
    );
    document.documentElement.removeEventListener(
      'keydown',
      this.onKeyDown,
      false
    );
    document.documentElement.removeEventListener('keyup', this.onKeyUp, false);
    document.documentElement.removeEventListener(
      'mousemove',
      this.onMouseMove,
      false
    );
  }

  // Mac ctrl-click by default opens context menu. Prevent that.
  preventContextMenu = e => {
    if (process.env.NODE_ENV !== 'development') {
      if (e.target === this.catcherRef.current) {
        e.preventDefault();
      }
    }
  };

  onKeyDown = e => {
    if (e.key === 'Control') {
      this.setState({ ctrlPressed: true });
    }
  };

  onKeyUp = e => {
    if (e.key === 'Control') {
      this.setState({ ctrlPressed: false });
    }
  };

  onMouseDown = e => {
    if (!e.ctrlKey) {
      if (process.env.NODE_ENV === 'development') {
        console.warn(
          "Clicked CtrlClickCatcher without ctrl key! Shouldn't happen."
        );
      }
      this.setState({ ctrlPressed: false, savedX: null, delta: null });
      return;
    }

    this.setState({ savedX: e.nativeEvent.offsetX, delta: 0 }, () => {
      document.documentElement.addEventListener(
        'mousemove',
        this.onMouseMove,
        false
      );
    });
  };

  onMouseMove = e => {
    this.setState({
      ctrlPressed: e.ctrlKey,
      delta: e.offsetX - this.state.savedX,
    });
  };

  onMouseUp = e => {
    const { nameForAnnotation, secondWidth, currentPosition } = this.props;
    const { savedX, delta } = this.state;
    const startX = delta >= 0 ? savedX : savedX + delta;
    const width = Math.abs(delta);

    this.setState({ ctrlPressed: e.ctrlKey, savedX: null, delta: null }, () => {
      const id = new Date().getTime(); // TODO come up with ID reconciliation between external app and this library
      this.props.dispatch(
        createAnnotation({
          id,
          name: nameForAnnotation,
          time: (currentPosition + startX) / secondWidth,
          duration: width / secondWidth,
        })
      );
      this.props.dispatch(selectAnnotation(id));
    });
  };

  render() {
    const { viewportHeight } = this.props;
    const { ctrlPressed, savedX, delta } = this.state;
    return (
      <div
        className="ctrlClickCatcher"
        ref={this.catcherRef}
        style={{
          height: viewportHeight,
          pointerEvents: ctrlPressed || savedX !== null ? 'unset' : 'none',
        }}
        onMouseDown={this.onMouseDown}
        onMouseUp={this.onMouseUp}
      >
        <NewSelection savedX={savedX} delta={delta} />
      </div>
    );
  }
}

const mapStateToProps = state => ({
  viewportHeight: state.presentation.viewportHeight,
  nameForAnnotation: (
    getSelectedAnnotation(state) ||
    state.annotations.list[0] || { name: 'New annotation' }
  ).name,
  secondWidth: getSecondWidth(state),
  currentPosition: getCurrentPosition(state),
});

export default connect(mapStateToProps, undefined, undefined, {
  storeKey: STORE_KEY,
})(CtrlClickCatcher);
